.success_img {
    margin-bottom: 45px;
  }
  .title.success {
    margin-bottom: 45px;
  }
  .success {
   
    padding-top: 42px;
    padding-bottom: 164px;
  }
  
  .container.success {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-width: 411px;
    padding: 0 20px;

  }
  .title.success {
    font-family: 'Merriweather', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 58px;
    line-height: 1.15;
    text-align: center;
    color: #393936;
    @media screen and (min-width: 441px) {
      font-size: 30px;
    }
  }
  .btn-success{
    width: 237px;
    height: 50px;
    font-family: 'TT Commons', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 19px;
    line-height: 1.25;
    color: #ffffff;
    background: #ff6f41;
    box-shadow: 0px 10px 30px rgba(255, 111, 65, 0.5);
    border-radius: 200px;
  }
  .logo {
      display: inline-block;
    }
    
    .logo_icon {
      width: 134px;
      height: 30px;
     
    }
    